.App {
  width: 100vw;
  height: 100vh;
}
body {
  margin: 0;
  padding: 0;
}
button:hover {
  cursor: pointer;
}
.Navbar
{
  background-color: cornflowerblue;
  width: 100%;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}
.Navbar .leftSide {
  flex: 50%;
  display: flex;
  justify-content: left;
  align-items: center;
  
}
.Navbar .rightSide {
  flex: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 25px;
  
}
.Navbar .leftSide .links {
  max-height: 80px;
 

}
.Navbar .leftSide .links a {
  text-decoration: none;
  color: white;
  font-size: 25px;
  margin-left: 15px;

}

.rightSide input{
  width: 220px;
  height: 30px;
  border: none;
  border-radius: 4px;
  font-size: 20px;
  padding-left: 15px;
  color: black;
}
.rightSide button{
  height: 33px;
  width: 70px;
  font-size: 16px;
}



@media only screen and (max-width: 850px)
{
  .Navbar .leftSide .links a {
    font-size: 20px;
  }
  }
@media only screen and (max-width: 750px)
{
  .Navbar .leftSide .links a {
    font-size: 15px;
  }
  }
